.title{
    justify-content: center;
    align-items: center;
    text-align: center;
    margin-top: 40px;
    margin-bottom: 40px;
    height: 45px;
    background-color: rgb(135, 201, 135);
    color: ivory;
    border-radius: 4px;    
}

#input{
    font-size: 25px;
    min-width: 300px;
    min-height: 50px;
    margin-bottom: 20px;
}

.search_wrapper{
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
}

.params-items{
    display: inline;
    font-size: 20px;
    margin-bottom: 25px;
}

.flag{
    display: block;
    height: 150px;
    margin-left: 80px;
    padding: 10px;
}

.country-wrapper{
    display: flex;

}

.result_title{
    text-align: center;
    margin-bottom: 30px;
}

.card_title{
    text-align: center;
}

.pnotify-container{
    margin-left: 37%;
}